<template>
	<view class="wrapper padding">
		
		<view class="list"  v-if="!empty">
			<view class="item" v-for="(item,index) in listData" :key="index">
				<view class="info">
					<image src="../../static/default_avatar.png" class="avatar"></image>
					<view class='other'>
						<view class='name_w'>
							<text class="name">{{item.name}}</text>
							<text :class="['status',item.isBind==0?'unpass':'pass']">
							{{item.isBind==0?'未录入':'已录入'}}
							</text>
						</view>
						<view class='tel'>{{item.mobile}}</view>
					</view>
				</view>
				<view class="go" v-if="item.isBind==0" @click="goDesc(item)">
					<view class="btn">
						<button class="cu-btn luru">
							信息录入
						</button>
					</view>
				</view>
			</view>
			<loadMore :status="status" :content-text="textDesc"></loadMore>
		</view>
		<view class="no_data" v-else>
			暂无数据
		</view>
	</view>
</template>
<script>
	import request from '../../common/request.js'
	import { formatTime,http2Https } from '../../common/tools.js'
	import loadMore from "../../components/loadMore/loadMore.vue"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"loading", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				empty:false,
				listData:[],
				name:''
			};
		},
		methods:{
			getList(){
				let _readArr=uni.getStorageSync('readArr');
				request("/staff/userlist",{name:this.name}).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data.length>0){ //如果有数据
							this.empty=false
							this.listData=_data
							this.status="noMore"
						}else{ //如果这次没数据
							this.empty=true
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			
			goDesc(item){
				this.perfectNavigateTo({
					url:"/pages/staff/createOrder?fromType=0&info="+JSON.stringify(item)
				})
			}
		},
		components:{
			loadMore
		},
		onLoad(params){
			this.name=params.name
		},
		onShow(){
			this.getList()
		}
	}
</script>

<style lang="stylus" scoped>
	.wrapper
		.list
			.item
				display:flex
				flex-direction row
				justify-content space-between
				height:200upx
				align-items center
				.info
					display:flex
					flex-direction row
					align-items center
					.avatar
						width:108upx
						height:108upx
						border-radius 50%
						overflow hidden
						margin-right:40upx
					.other
						.name_w
							display:flex
							flex-direction row
							align-items center
							margin-bottom:10upx
							.name
								font-size:32upx;
								font-family:PingFangSC-Semibold,PingFang SC;
								font-weight:600;
								color:rgba(30,30,30,1);
								margin-right:20upx
							.status
								height:30upx;
								line-height 30upx
								text-align center
								border-radius:4upx;
								padding:5upx 10upx
								font-size:24upx
								color:#fff
								&.unpass
									background:rgba(112,208,151,1);
								&.pass
									background:#B2B6C0
				.go
					.luru
						background:#1F2334
						color:#fff
						letter-spacing 2upx
</style>				
<style lang="stylus">
	.no_data
		text-align:center
		font-size:32upx;
		font-family:PingFangSC-Regular;
		font-weight:400;
		margin-top:30%
		color:rgba(203,210,219,1);
</style>